.MuiMobileStepper-dotActive-135 {
  background: white !important;
}

.MuiPrivateTabIndicator-colorPrimary-105{
    background-color: white !important;
}
.recommend_row {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  padding: 0 12px;
  overflow: hidden;
  overflow-x: scroll;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000;
  -webkit-overflow-scrolling: touch;
}
.recommend_row::-webkit-scrollbar {
  display: none;
}

.recommend_container {
  font-size: 12px;
  margin: 20px 8px;
  /* padding-right: 15px; */
}
.recommend_container:last-child{
  padding-right: 15px;
}

.recommend_wrapper {
  width: 120px;
  height: 120px;
}

.recommend-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.gamePay_container {
  height: 100vh;
  width: 100%;
}

.gamePay_menu {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 9999;
  background: white;
  border-radius: 50%;
  color: black;
  z-index: 99999;
}

.setting {
  width: 50%;
  height: 100px;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  position: absolute;
  right: 60px;
  top: 60px;
  padding: 0 20px;
  z-index: 999999;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.setting-back,
.setting-home {
  background: white;
  color: black;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  text-align: center;
}

.setting-text{
  color: white;
}

body {
  background-color: #f5f5f9;
  font-family: Roboto, Lato, sans-serif !important;
  
}


